body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  display: flex;
  justify-content: space-between;
  padding: 20px 80px;
  background-color: black;
  color: white;
}
.cart-icon{
  display: flex;

}
.cart-icon .counter{
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  right: 8px;
  bottom: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon{
  font-size: 24px;
}
 img{
  height: 150px;
 }
 .phones{
  display: inline-flex;
  width: 320px;
  height: 200px;
  box-shadow: 0px 0px 7px black;
  border-radius: 10px;
  margin: 40px;
 }
 .img{
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-right: 1px solid rgb(185, 184, 184);
 }
 button{
  justify-content: bottom;
 }

 .inner{
  display: flex;
  flex-direction: column;
  padding: 10px;
  gap: 10px;
  flex-grow: 1;
 }
 .price{
  margin-bottom: auto;
 }
 .inner button{
  background-color: black;
  color: white;
  border-radius: 3px;
 }

.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid grey;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity button{
  height: 25px;
  width: 25px;
}
.quantity > div:first-child, .quantity > button{
  background-color:black;
  color: white;
  border: 0;
}
.quantity > button:disabled{
  opacity: 0.5;
}
button{
  cursor: pointer;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
}
.tot{
  background-color: rgb(209, 209, 209);
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: flex-end;
  bottom: 0;
  width: 100%;
  padding: 10px;
}
.total{
  margin-right: 50px;
}